import React, { useEffect, useState } from 'react';
import ModalDynamic from '../../Components/Modal';

const Editar = ({ show, onHide, item }) => {
  const [formData, setFormData] = useState({});
  const fields = [
    { name: 'nome', label: 'Nome', type: 'text', placeholder: 'Nome Completo', required: true, pattern: '^([A-Za-z--]+(?: [A-Za-z--]+)*)$', step: 1 },
    { name: 'email', label: 'Email', type: 'email', placeholder: 'Email', required: true, step: 1 },
    {
      name: 'qualificacao',
      label: 'Qualificao',
      type: 'text',
      required: true,
      step: 1
    }
  ];

  useEffect(() => {
    setFormData(item);
  }, [item]);

  return formData && Object.keys(formData).length > 0 && (
    <ModalDynamic
      title={'Editar Usurio'}
      show={show}
      onHide={onHide}
      fields={fields}
      post={'usuario'}
      formData={formData}
    />
  );
};

export default Editar;